<template>
    <div class="content-dashboard">
        <el-row>
            <el-col :span="7" class="el-m">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>Application</span>
                    </div>
                    <div class="ad">
                        <span class="center-item">{{ cApp }}</span>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="7" class="el-m">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>Category</span>
                    </div>
                    <div class="ad">
                        <span class="center-item">{{ cAppCat }}</span>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="20" class="el-m">
                <el-card class="box-card">
                <el-table
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                        prop="name_applications"
                        label="Name Application"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="key_applications"
                        label="Key">
                    </el-table-column>
                </el-table>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>

import axios from '../../axios.js'

export default {
    data () {
        return {
            tableData: [],
            cAppCat : '0',
            cApp : '0'
        }
    },
    created () {
        const accessToken = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        }
        axios.get(`dashboard/data/${this.$store.state.userDataActive.id}/show`, accessToken)
        .then((response) => {
            if (response.data.success == true) {
              this.tableData = response.data.data
              this.cAppCat = response.data.countCatApp
              this.cApp = response.data.countApp
            }
        })
    }
}
</script>


<style>
.el-m {
    margin: 5px;
}
.ad {
    margin: auto;
    font-size: 32px;
    text-align: center;
    color: #909399;
    opacity: 80%;
}
.content-dashboard {
    margin: 0;
    padding: 25px;
}
</style>